                background-color: rgba(0, 0, 0, 0.6);
                text-align: center;
                max-height: 250px;
                border-radius: 5px;
                display: inline-block;
                margin: 0 20px;

                .message-body-content {
                    padding: 0;
                    color: #fff;
                    display: inline-block;
                    text-align: initial;
                }
            }
        }
        /* , start */
        &.alert,
        &.confirm,
        &.prompt {
            .message-content {
                width: 270px;
                border-radius: 10px;

                .message-header .message-title {
                    font-size: 17px;
                    font-weight: bold;
                    color: #000;
                    text-align: center;
                }

                .message-body {
                    min-height: 60px;
                    line-height: 25px;
                    font-size: 15px;
                }
            }
        }
        /* , end */

        /*  start */
        &.alert .message-content .message-footer .message-button.alert {
            width: 100%;
            text-align: center;
            color: #09bb07;
        }
        /*  end */

        /*  start */
        &.confirm .message-content .message-footer,
        &.prompt .message-content .message-footer {
            color: #666;

            .message-button:first-child {
                border-right: 1px solid #e4e4e4;
                box-sizing: border-box;
            }

            .message-button {
                width: 50%;
                float: left;
            }

            .message-button.ok {
                color: #09bb07;
                font-weight: bold;
            }
        }

        &.prompt .message-content {
            .message-header .message-title {
                font-size: 15px;
                font-weight: normal;
                color: #000;
            }

            .message-body .message-body-content input.input-text {
                border: none;
                outline-offset: 0;
                outline-style: none;
                outline-width: 0;
                font-size: 14px;
                line-height: 25px;
                height: 25px;
            }
        }

        /*  end */

        /*  start*/
        &.multiple .message-content {
            width: 90%;
            max-width: 450px;

            .message-footer {
                color: #09bb07;
                display: flex;
                justify-content: space-between;

                .message-button {
                    flex: 1;
                }

                .message-button:not(:last-child) {
                    border-right: 1px solid #e4e4e4;
                    box-sizing: border-box;
                }
            }
        }
        /*  end*/

        /*  start*/
        &.vertical .message-content {
            width: 90%;
            max-width: 450px;

            .message-footer {
                color: #09bb07;
                height: auto;

                .message-button {
                    width: 100%;
                    height: 42px;
                }

                .message-button:not(:first-child) {
                    border-top: 1px solid #e4e4e4;
                    box-sizing: border-box;
                }
            }
        }
        /*  end*/

        &.bottom-sheet {
            vertical-align: bottom;

            .message-content {
                width: 100%;
                max-width: 540px;
                border-radius: 0px;

                .message-footer {
                    color: #09bb07;
